import { Component, OnInit, HostListener } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-page-before-form',
  templateUrl: './page-before-form.component.html',
  styleUrls: ['./page-before-form.component.scss']
})
export class PageBeforeFormComponent implements OnInit {
  text: string;
  text2: string;
  screenWidth;
  constructor(private http: HttpClient) {}

  ngOnInit() {
    this.http
      .get('assets/text/page-before-form-text.txt', { responseType: 'text' })
      .subscribe(data => (this.text = data));
    this.http
      .get('assets/text/page-before-form-text-2.txt', { responseType: 'text' })
      .subscribe(data => (this.text2 = data));
    this.screenWidth = window.innerWidth;
  }
  @HostListener('window:resize', ['$event'])
  getScreenSize(event?) {
    this.screenWidth = window.innerWidth;
  }
}
